/**
 * API Client - API
 * API
 */

// APIURL
// localhost:5101
// 
const getApiBaseUrl = (): string => {
    const envApiUrl = import.meta.env.VITE_API_BASE_URL;

    // API
    if (envApiUrl && envApiUrl.trim()) {
        return envApiUrl;
    }

    // localhost:5101
    if (import.meta.env.DEV) {
        return "http://localhost:5101";
    }

    // 
    return window.location.origin;
};

export const API_BASE_URL = getApiBaseUrl();

/**
 * 
 */
interface RequestOptions extends RequestInit {
    params?: Record<string, string | number | boolean>;
}

/**
 * URLquery
 */
const buildUrl = (endpoint: string, params?: Record<string, string | number | boolean>): string => {
    // endpoint/
    const path = endpoint.startsWith("/") ? endpoint : `/${endpoint}`;
    const url = new URL(path, API_BASE_URL);

    if (params) {
        Object.entries(params).forEach(([key, value]) => {
            url.searchParams.append(key, String(value));
        });
    }

    return url.toString();
};

/**
 * fetch
 */
const request = async <T = any>(
    endpoint: string,
    options: RequestOptions = {}
): Promise<T> => {
    const { params, ...fetchOptions } = options;

    const url = buildUrl(endpoint, params);

    // 
    const config: RequestInit = {
        credentials: "include", // cookies
        headers: {
            "Content-Type": "application/json",
            ...fetchOptions.headers,
        },
        ...fetchOptions,
    };

    const response = await fetch(url, config);

    // 401
    if (response.status === 401) {
        // 
        console.warn("Unauthorized request, please login");
    }

    // JSON
    const contentType = response.headers.get("content-type");
    if (contentType && contentType.includes("application/json")) {
        return response.json();
    }

    // JSON
    return response.text() as any;
};

/**
 * API Client 
 */
export const apiClient = {
    /**
     * GET 
     */
    get: <T = any>(endpoint: string, params?: Record<string, string | number | boolean>) => {
        return request<T>(endpoint, { method: "GET", params });
    },

    /**
     * POST 
     */
    post: <T = any>(endpoint: string, body?: any, options?: RequestOptions) => {
        return request<T>(endpoint, {
            method: "POST",
            body: body ? JSON.stringify(body) : undefined,
            ...options,
        });
    },

    /**
     * PUT 
     */
    put: <T = any>(endpoint: string, body?: any, options?: RequestOptions) => {
        return request<T>(endpoint, {
            method: "PUT",
            body: body ? JSON.stringify(body) : undefined,
            ...options,
        });
    },

    /**
     * DELETE 
     */
    delete: <T = any>(endpoint: string, options?: RequestOptions) => {
        return request<T>(endpoint, { method: "DELETE", ...options });
    },

    /**
     * PATCH 
     */
    patch: <T = any>(endpoint: string, body?: any, options?: RequestOptions) => {
        return request<T>(endpoint, {
            method: "PATCH",
            body: body ? JSON.stringify(body) : undefined,
            ...options,
        });
    },
};

/**
 * fetch
 */
export const apiFetch = async (endpoint: string, options?: RequestInit) => {
    const url = buildUrl(endpoint);
    return fetch(url, {
        credentials: "include",
        ...options,
    });
};

export default apiClient;

